const loc = window.location;
const URL = `${loc.protocol}//${loc.hostname}/api`;
const headers = { "Content-Type": "application/json" };
const httpReq = (endPoint, method, body) => {
    const opts = {
        method: method,
        headers: headers,
    };
    if (body) {
        opts["body"] = JSON.stringify(body);
    }
    const results = fetch(URL + endPoint, opts);
    console.log(results);
    return results;
};
const get = (endPoint) => {
    return httpReq(endPoint, "GET");
};
const post = (endPoint, body) => {
    return httpReq(endPoint, "POST", body);
};
/* delete is a reserved keyword */
const remove = (endPoint) => {
    return httpReq(endPoint, "DELETE");
};
const patch = (endPoint, body) => {
    return httpReq(endPoint, "PATCH", body);
};
export { get, post, remove, patch };
